import AppDispatcher from '../dispatcher/AppDispatcher';
import Store from './Store';
import ActionTypes from '../constants/ActionTypes';

var _id = null;
var _name = '';

function _update(id, name) {
	_id = id;
	_name = name;
}

function _updateName(name) {
	_name = name;
}

function _reset() {
	_id = null;
	_name = '';
}

class _AccountStore extends Store {
	
	getAll() {
		return {
			id: _id,
			name: _name
		};
	}
	
	getID() {
		return _id;
	}
	
	getName() {
		return _name;
	}
}

const AccountStore = new _AccountStore();

AccountStore.dispatchToken = AppDispatcher.register(payload => {

	switch( payload.actionType ) {
			
		case ActionTypes.RECEIVE_ACCOUNT:
			_update(payload.id, payload.name);
			break;
			
		case ActionTypes.UPDATE_USERNAME:
			_updateName(payload.username);
			break;

		case ActionTypes.LOGOUT_SUCCESS:
			_reset();
			break;
		
		// Testing purpose:
		case ActionTypes.RECEIVE_RAW_LISTS:
			_update(4, 'Elytherion');
			break;
			
		default:
			return true;
	}
	
	AccountStore.emitChange();

	return true;

});

export default AccountStore;



