import AvatarWrapper from '../AvatarWrapper';
import React, { Component, PropTypes } from 'react';
import Tab from '../../components/Tab';
import TabActions from '../../actions/TabActions';

export default class TitleBarNav extends Component {

	static propTypes = {
		active: PropTypes.string.isRequired,
		avatar: PropTypes.string,
		tabs: PropTypes.array.isRequired
	};

	handleClick = tab => TabActions.showTab(tab);

	render() {

		const { active, avatar, children, tabs } = this.props;

		const avatarElement = avatar ? <AvatarWrapper src={avatar} /> : null;

		return (
			<div className="left">
				{avatarElement}
				{children}
				{
					tabs.map(tab => {
						const { tag, ...other } = tab;
						const isActive = active === tag;

						return (
							<Tab
								onClick={this.handleClick.bind(null, tag)}
								{...other}
								key={`tab-${tag}`}
								active={isActive}>
							</Tab>
						);
					})
				}
			</div>
		);
	}
}



