import AppDispatcher from '../dispatcher/AppDispatcher';
import Store from './Store';
import { get, getAllByCategory } from './ListStore';
import RaceStore from './RaceStore';
import ActionTypes from '../constants/ActionTypes';
import Categories from '../constants/Categories';

const CATEGORY = Categories.ATTRIBUTES;

var _le = 0;
var _le_add = 0;
var _ae_add = 0;
var _ke_add = 0;
var _sk = 0;
var _zk = 0;
var _gs = 0;

function _addMaxEnergyPoint(id) {
	switch (id) {
		case 'LE':
			_le_add++;
			break;
		case 'AE':
			_ae_add++;
			break;
		case 'KE':
			_ke_add++;
			break;
	}
}

function _clear() {
	_le = 0;
	_le_add = 0;
	_ae_add = 0;
	_ke_add = 0;
	_sk = 0;
	_zk = 0;
	_gs = 0;
}

function _updateAll(obj) {
	_le = obj.le;
	_le_add = obj.le_add;
	_ae_add = obj.ae_add;
	_ke_add = obj.ke_add;
	_sk = obj.sk;
	_zk = obj.zk;
	_gs = obj.gs;
}

function _assignRCP() {
	let currentRace = RaceStore.getCurrent() || { le: 0, sk: 0, zk: 0, gs: 0 };
	_le = currentRace.le;
	_sk = currentRace.sk;
	_zk = currentRace.zk;
	_gs = currentRace.gs;
}

class _AttributeStore extends Store {

	get(id) {
		return get(id);
	}

	getValue(id) {
		return get(id).value;
	}

	getAdd(id) {
		switch (id) {
			case 'LE':
				return _le_add;
			case 'AE':
				return _ae_add;
			case 'KE':
				return _ke_add;
			default:
				return 0;
		}
	}

	getAllForView() {
		return getAllByCategory(CATEGORY);
	}

	getBaseValues() {
		return {
			le: _le,
			leAdd: _le_add,
			aeAdd: _ae_add,
			keAdd: _ke_add,
			sk: _sk,
			zk: _zk,
			gs: _gs
		};
	}

	getSum() {
		return getAllByCategory(CATEGORY).reduce((a,b) => a + b.value, 0);
	}

	getForSave() {
		return Object.assign({}, {
			values: getAllByCategory(CATEGORY).map(e => [e.id, e.value, e.mod])
		}, this.getBaseValues());
	}

}

const AttributeStore = new _AttributeStore();

AttributeStore.dispatchToken = AppDispatcher.register(payload => {

	switch( payload.actionType ) {

		case ActionTypes.CLEAR_HERO:
		case ActionTypes.CREATE_NEW_HERO:
			_clear();
			break;

		case ActionTypes.RECEIVE_HERO:
			_updateAll(payload.attr);
			break;

		case ActionTypes.ADD_ATTRIBUTE_POINT:
		case ActionTypes.REMOVE_ATTRIBUTE_POINT:
			break;

		case ActionTypes.ADD_MAX_ENERGY_POINT:
			_addMaxEnergyPoint(payload.id);
			break;

		case ActionTypes.ASSIGN_RCP_ENTRIES:
			_assignRCP(payload.selections);
			break;
		
		default:
			return true;
	}
	
	AttributeStore.emitChange();

	return true;

});

export default AttributeStore;



